
body{
color: #576366;
    background-color: #5fcf80;
    margin: 0;
    font-family: Roboto,sans-serif;
}



.container{

	width: 95%;
    max-width: 780px;
    padding-bottom: 2em;
    margin: 0 auto 2em;
    background: #fff;
    border-radius: .5em;
    -webkit-box-shadow: 0 3px rgba(0,0,0,.1);
    box-shadow: 0 3px rgba(0,0,0,.1);
    overflow: hidden;
}

teacher-img {
    width: 10%;
    border-radius: .3em;}



.course {
    margin-bottom: 1em;
}

img {
    vertical-align: middle;
    width: 100%}
li{
list-style: none;
}



